import React from 'react'
import { Row, Col } from 'antd'
import L from 'leaflet'
import styled from 'styled-components'
import { Page } from '@/components/layout'
import Collapse from '@/components/collapse'
import withViewport from '@/components/withViewport'
import { envStore } from '@/constants/env'
import Map from './map'
import 'leaflet-draw/dist/leaflet.draw'

class ImageViewerTutorial extends React.Component {
  state = {
    mapCenter: null,
    score: 0,
  }

  constructor(props) {
    super(props)
    this.map = null
  }

  whenMapReady = map => {
    this.map = map

    map.setMaxBounds(map.getBounds())

    let drawnItems = L.featureGroup().addTo(map)

    map.addControl(
      new L.Control.Draw({
        position: 'topright',
        edit: {
          featureGroup: drawnItems,
          poly: {
            allowIntersection: false,
          },
        },
        draw: {
          polygon: true,
          marker: false,
          rectangle: true,
          polyline: true,
          circlemarker: false,
          circle: {
            shapeOptions: {
              color: '#00ff00',
              opacity: 1,
              fillOpacity: 0,
            },
          },
        },
      })
    )

    map.on(L.Draw.Event.CREATED, event => {
      let layer = event.layer

      layer.options.color = '#00FF00'
      layer.options.opacity = 1
      layer.options.fillOpacity = 0

      this.setState({ score: Math.floor(Math.random() * 100) })

      drawnItems.addLayer(layer)
    })
  }

  render() {
    const { mapCenter, score } = this.state
    const { viewport } = this.props

    return (
      <PageWrapper className="pr-10" style={{ height: viewport.height - 204 }}>
        <Row gutter={24}>
          <Col span={18}>
            <div className="bg-white rounded-lg shadow-2xl">
              <Map
                zoomControl={false}
                mapUrl={`${envStore.publicUrl}/images/HCC_image.png`}
                center={mapCenter}
                whenMapReady={this.whenMapReady}
              />
            </div>
          </Col>
          <Col span={6}>
            <Collapse
              activeKey={[1, 2]}
              expandIcon={() => null}
              className="shadow-2xl">
              <Collapse.Panel
                key={1}
                header={<div className="text-center">基本資料</div>}>
                <div className="border-b border-gray-600 p-3">姓名：xxxxxx</div>
                <div className="border-b border-gray-600 p-3">性別：男</div>
                <div className="border-b border-gray-600 p-3">
                  病理號：xxxxxxxxx
                </div>
                <div className="border-b border-gray-600 p-3">年齡：30</div>
                <div className="border-b border-gray-600 p-3">
                  疾病名稱：HCC
                </div>
              </Collapse.Panel>
              <Collapse.Panel
                key={2}
                header={<div className="text-center">分數計算</div>}>
                <div className="border-b border-gray-600 p-3">
                  分數：{score}
                </div>
              </Collapse.Panel>
            </Collapse>
          </Col>
        </Row>
      </PageWrapper>
    )
  }
}

const PageWrapper = styled(Page)`
  .leaflet-container {
    height: 600px;
    width: 100%;
    margin: 0 auto;
    background: #000;
    border-radius: 6px;
  }
`

export default withViewport(ImageViewerTutorial)
