class MapComponent extends React.PureComponent {
  static defaultProps = {
    zoomControl: true,
    attributionControl: false,
    doubleClickZoom: false,
    scrollWheelZoom: true,
    maxZoom: 1.5,
    minZoom: -1.0,
    crs: CRS.Simple,
    mapUrl: '',
  }

  state = {
    mapBounds: [
      [0, 0],
      [800, 1000],
    ],
  }

  whenMapReady = ({ target }) => {
    if (this.props.whenMapReady) {
      this.props.whenMapReady(target)
    }
  }

  // handleClick = e => {
  //   console.log(e)
  //   if (this.props.onClick) {
  //     this.props.onClick(e)
  //   }
  // }

  // handleDrop = item => {
  //   if (this.props.onDrop) {
  //     this.props.onDrop(item)
  //   }
  // }

  // handleZoomStart = e => {
  //   if (length(document.querySelectorAll('.react-contexify')) > 0) {
  //     contextMenu.hideAll()
  //   }
  // }

  render() {
    const {
      props: {
        mapUrl,
        center,
        zoomControl,
        zoom,
        minZoom,
        maxZoom,
        scrollWheelZoom,
        doubleClickZoom,
        // connectDropTarget,
      },
      state: { mapBounds },
    } = this

    return (
      <React.Fragment>
        <MapContainer
          id="map"
          crs={CRS.Simple}
          bounds={mapBounds}
          center={center}
          zoom={zoom}
          maxZoom={maxZoom}
          minZoom={minZoom}
          zoomDelta={0.5}
          zoomSnap={0.5}
          zoomControl={zoomControl}
          // onZoomStart={this.handleZoomStart}
          scrollWheelZoom={scrollWheelZoom}
          doubleClickZoom={doubleClickZoom}
          attributionControl={false}
          // onClick={this.handleClick}
          whenReady={this.whenMapReady}>
          <ImageOverlay url={mapUrl} bounds={mapBounds} />
          {this.props.children}
        </MapContainer>
      </React.Fragment>
    )
  }
}

export default MapComponent
